*{margin:0;padding:0;border:0;font:inherit;color:inherit;text-align:inherit;text-decoration:inherit;vertical-align:inherit;text-transform:inherit;background:transparent;box-sizing:border-box}ol,ul{list-style:none}img{display:block;height:auto;width:100%}input[type="text"],input[type="password"],input[type="submit"],textarea,button{-webkit-appearance:none;-moz-appearance:none;appearance:none}button,a,input[type="submit"]{cursor:pointer}body{font-family:sans-serif}.hidden{display:none}@keyframes shake{0%{transform:rotate(0deg)}12.5%{transform:rotate(30deg)}25%{transform:rotate(0deg)}37.5%{transform:rotate(-30deg)}50%{transform:rotate(0deg)}50%{transform:rotate(0deg)}62.5%{transform:rotate(30deg)}75%{transform:rotate(0deg)}87.5%{transform:rotate(-30deg)}100%{transform:rotate(0deg)}0%{opacity:1}100%{opacity:0}}@keyframes fade-in-out{0%{background-color:initial}50%{background-color:#fda}100%{background-color:initial}}.gm-header{width:95vw;min-height:8vh;display:flex;align-items:center;justify-content:flex-end}.gm-header button{color:#995c00;background:#ffe7c3;border-radius:15px;font-weight:700;padding:10px;margin-left:20px;border:1px solid #fda;box-shadow:2px 3px 0px 0px #f4a125}.gm-header button:hover,.gm-header button:focus{transform:translate(-0.5px, -0.5px);box-shadow:3.5px 4.5px 0px 0px #f4a125}.gm-header button:focus{outline:none}@media only screen and (min-width: 700px){.gm-header{width:42vw;min-height:calc(40px + 1.5vw)}.gm-header .hd-menu{display:none}}.game{position:relative;display:flex;flex-direction:column;align-items:center;height:100vh}.gm-components{display:flex;justify-content:center;align-items:center;width:100%;position:relative;margin-bottom:auto}@media only screen and (min-width: 700px){.game{height:calc(100vh - 40px)}}@media only screen and (min-width: 700px) and (max-width: 1050px){.gm-components{display:grid;grid-template:repeat(2, 30vw)/1fr 60vw 1fr 35vw 1fr}}.gm-puzzle{display:grid;grid-template:repeat(9, 1fr)/repeat(9, 1fr);width:95vw;height:95vw;border-right:4px solid black;border-bottom:4px solid black;position:relative;z-index:1;background:white}.gm-puzzle:focus{outline:none}.gm-square{position:relative;height:100%;width:100%;border-top:1px solid grey;border-left:1px solid grey}.gm-square.focused{background-color:#ddfcff}.gm-square.group{background-color:#ffe0dd}.gm-square.conflicting{animation:fade-in-out 600ms 1}.gm-square::before{position:absolute;bottom:-2px;top:-2px;right:-2px;left:-2px;display:block;content:''}.gm-square input{position:relative;z-index:1;height:100%;width:100%;padding-top:12px;text-align:center;caret-color:transparent;font-size:calc(95vw / 9 / 2);cursor:pointer}.gm-square input:focus{outline:none}.gm-square input.mistake{animation:shake 600ms ease-in-out 50ms 1}.left-bound::before{border-left:4px solid black}.top-bound::before{border-top:4px solid black}.cage-label{position:absolute;font-weight:bold;left:5%;top:7%;font-size:calc(95vw / 9 / 4.5)}@media only screen and (min-width: 700px) and (max-width: 1050px){.gm-puzzle{height:60vw;width:60vw;grid-area:1 / 2 / 3 / 3}.cage-label{font-size:calc(60vw / 9 / 4.5)}.gm-square input{font-size:calc(60vw / 9 / 2)}}@media only screen and (min-width: 1050px){.gm-puzzle{height:42vw;width:42vw}.cage-label{font-size:calc(42vw / 9 / 4.5)}.gm-square input{font-size:calc(42vw / 9 / 2)}}.gm-info{width:70vw;height:70vw;border:4px solid black;border-radius:10px;background-color:white}@media only screen and (max-width: 700px){.gm-info{position:absolute;top:50%;bottom:50%;left:50%;right:50%;transform:translate(-50%, -50%)}}@media only screen and (min-width: 700px) and (max-width: 1050px){.gm-info{height:calc(60vw / 2 - 5px);width:35vw}.gm-info.gm-info-sqr{grid-area:1 / 4 / 2 / 5}.gm-info.gm-info-grp{grid-area:2 / 4 / 3 / 5}}@media only screen and (min-width: 1050px){.gm-info{height:27vw;width:27vw;margin:auto}}.gm-info-sqr{background:#ddfcff;border:2px solid #00818f}.gm-info-sqr.swipe-show{visibility:visible}.gm-info-nums{display:flex;flex-wrap:wrap;width:100%}.info-num-wrapper{font-size:2rem;display:flex;justify-content:center;align-items:center}.info-num{color:black;opacity:0.2}.info-num:hover{cursor:pointer}.info-num.selected{color:#ffc9c3;opacity:1}.info-num.ruled-out{visibility:hidden}.info-nums-checkall{height:50px;width:50px;font-size:2rem;display:flex;justify-content:center;align-items:center;color:black;opacity:0.2;cursor:pointer}.info-nums-checkall.selected{color:#ffc9c3;opacity:1}@media only screen and (min-width: 700px){.gm-info-sqr{animation:slide-left 200ms ease-out 1, scale-up 200ms ease-in-out 200ms 1}@keyframes slide-left{0%{opacity:0.6;transform:translateX(180%) scale(0.4);position:relative;z-index:-1}100%{opacity:0.6;transform:translateX(0) scale(0.4)}}@keyframes slide-right{0%{opacity:0.6;transform:translateX(-180%) scale(0.4);position:relative;z-index:-1}100%{opacity:0.6;transform:translateX(0) scale(0.4)}}@keyframes scale-up{0%{opacity:0.6;transform:scale(0.4)}100%{opacity:1;transform:scale(1)}}.gm-info-sqr.show{display:flex}.gm-info-sqr.leaving{transform:translateX(90%) scale(0.7);opacity:.3;transition:transform 200ms ease, opacity 200ms ease}}.gm-info-grp{background:#ffb2aa;border:2px solid #800d00}.gm-info-grp.swipe-show{visibility:visible}@media only screen and (min-width: 700px){.gm-info-grp{animation:slide-right 200ms ease-out 1, scale-up 200ms ease-in-out 200ms 1}@keyframes slide-left{0%{opacity:0.6;transform:translateX(180%) scale(0.4);position:relative;z-index:-1}100%{opacity:0.6;transform:translateX(0) scale(0.4)}}@keyframes slide-right{0%{opacity:0.6;transform:translateX(-180%) scale(0.4);position:relative;z-index:-1}100%{opacity:0.6;transform:translateX(0) scale(0.4)}}@keyframes scale-up{0%{opacity:0.6;transform:scale(0.4)}100%{opacity:1;transform:scale(1)}}.gm-info-grp.show{display:flex}.gm-info-grp.leaving{transform:translateX(-90%) scale(0.7);opacity:0.3;transition:transform 200ms ease, opacity 200ms ease}}@media only screen and (min-width: 700px) and (max-width: 1050px){.gm-info-grp{align-self:end}}
